import React, { useState } from 'react';
import { View, Text, TextInput, TouchableOpacity, ActivityIndicator, StyleSheet } from 'react-native';

const App = () => {
  const [message, setMessage] = useState('');
  const [response, setResponse] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const handleSendClick = () => {
    // Input validation
    if (!message) {
      setError('Please enter a Roblox asset ID');
      return;
    }

    setLoading(true);
    setError('');

    const url = 'https://cors-proxy-kylenguyen7-d4dea47f740b.herokuapp.com/https://apis.roblox.com/cloud/v2/users/4739006702/inventory-items?filter=assetIds=' + message; // Replace with your API endpoint
    const headers = {
      'x-api-key': '1dkwuQUhUkiWR8NaH5kf38u2wzUyPw73ecHPeQD368/9ehkQ'
    };

    // Send HTTP request using fetch
    fetch(url, {
      method: 'GET', // Change the HTTP method if needed
      headers,
    })
      .then((response) => response.json())
      .then((data) => {
        setResponse(data.inventoryItems.length ? "Yes! chippskale owns item '" + message + "' 😁" :
                                                 "No, chippskale does not own item '" + message + "' 😔");
      })
      .catch((error) => {
        setError('An error occurred. Please try again later.');
        console.error('Error:', error);
      })
      .finally(() => {
        setLoading(false);
      });
  };

  return (
    <View style={styles.outerContainer}>
      <View style={styles.innerContainer}>
        <Text style={styles.title}>Does chippskale own my asset?</Text>
        <TextInput
          style={styles.input}
          placeholder="Enter a Roblox asset ID"
          value={message}
          onChangeText={(text) => setMessage(text)}
        />
        {error ? <Text style={styles.error}>{error}</Text> : null}
        <TouchableOpacity style={styles.sendButton} onPress={handleSendClick}>
          {loading ? (
            <ActivityIndicator size="small" color="#ffffff" />
          ) : (
            <Text style={styles.sendButtonText}>Send</Text>
          )}
        </TouchableOpacity>
        <Text style={styles.responseMessage}>{response}</Text>
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  outerContainer: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: '#f5f5f5',
    height: '100vh'
  },
  innerContainer: {
    backgroundColor: 'white',
    padding: 20,
    borderRadius: 10,
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 2 },
    shadowOpacity: 0.3,
    shadowRadius: 4,
    elevation: 3,
    alignItems: 'center',
  },
  title: {
    fontSize: 24,
    fontWeight: 'bold',
    marginBottom: 20,
    color: '#333',
  },
  input: {
    width: 300,
    height: 40,
    borderWidth: 1,
    borderColor: '#ccc',
    borderRadius: 5,
    padding: 10,
    marginBottom: 10,
  },
  sendButton: {
    backgroundColor: 'green',
    padding: 10,
    borderRadius: 5,
    alignItems: 'center',
    width: 100,
  },
  sendButtonText: {
    color: 'white',
    fontWeight: 'bold',
  },
  responseMessage: {
    marginTop: 10,
    fontSize: 18,
    color: '#333',
  },
  error: {
    color: 'red',
    marginBottom: 10,
  },
});

export default App;
